@import "https://fonts.google.com/specimen/Roboto+Slab?sidebar.open=true&selection.family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900";

* {
  font-family: Roboto, sans-serif;
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  outline-width: 0px;
  transition: .3s ease-in-out;
}
* > a {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
  font-size: 20px;
}
* > ui {
  list-style: none;
}
