@import url(https://fonts.google.com/specimen/Roboto+Slab?sidebar.open=true&selection.family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900);
* {
  font-family: Roboto, sans-serif;
  margin: 0px;
  padding: 0px;
  scroll-behavior: smooth;
  outline-width: 0px;
  transition: .3s ease-in-out;
}
* > a {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
  font-size: 20px;
}
* > ui {
  list-style: none;
}

.app {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  /* background: rgb(2,0,36); */
  /* background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 37%, rgba(0,212,255,1) 100%); */
  
}

.MuiBackdrop-root > .transfer__box {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 50px;
    height: 50vh;
    background-color: white;
}
.MuiBackdrop-root > .sending__money {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.357);
  align-items: center;
  justify-content: center;
}
.history  {
  width: 100%;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-with: 200px) and (max-width: 780px) {
  .table .MuiPaper-root .MuiTable-root {
    margin-top: 60%;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 10vh;
  transition: 0.5s ease-in-out;
  z-index: 10;
  align-items: center;
}

.navbar__inside {
  flex: 1 1;
  display: flex;

  align-items: center;
  justify-content: space-between;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  height: 100%;

  -webkit-padding-start: 20px;

          padding-inline-start: 20px;

  cursor: pointer;
}

.navbar__options {
  flex: 2 1;
  height: 100%;

  display: flex;
  -webkit-padding-end: 20px;
          padding-inline-end: 20px;
  flex-direction: row;

  align-items: center;
  justify-content: space-evenly;
}

.navbar__options > a {
  display: inline;
  font-size: 18px;
  margin-left: 20px;
  margin-right: 20px;
  cursor: pointer;
  transition: transform 0.2s ease-in;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  font-weight: 900;
}
.navbar__options > a:hover {
  z-index: 1000;
  text-shadow: 1px 1px 3px blue;
  transform: scale(1.08);
  color: #171c28;
}
.navbar__inside > .navbar__logo {
  width: 100px;
  height: 10vh;
  display: flex;
  align-items: center;
}
.navbar__inside > .navbar__menu {
  /* display; */
  opacity: 0;
  display: none;
  width: 100px;
  height: 10vh;
}
@media only screen and (min-width: 280px) and (max-width: 780px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    -webkit-padding-start: 20px;
            padding-inline-start: 20px;
    -webkit-padding-end: 20px;
            padding-inline-end: 20px;
    height: 70px;
    margin-bottom: 10px;
  }
  .navbar__inside {
    flex: 0.3 1;
    width: 100%;
    height: 10vh;
    align-items: center;
    justify-content: space-between;
  }
  .navbar__inside > .navbar__menu {
    display: flex;
    opacity: 1;
    align-items: center;
  }
  .navbar__options > li:nth-child(1) {
    margin-top: 20px;
  }
  .navbar__options > inputS {
    margin-bottom: 20px;
  }
  .navbar__options {
    width: 100%;
    height: 300px;
    flex-direction: column;
    align-items: center;
    justify-content: unset;
    transition: 0.5s ease-in-out;
  }
  .navbar__options > a {
    margin-top: 5px;
    margin-bottom: 20px;
  }
  .navbar__logo > span {
    display: flex;
    margin-right: 25px;
    padding: 10px;
    align-items: center;
    justify-content: center;
    border-radius: 40px;
  }
}

.inputS {
  width: 90px;
  height: 30px;
  border-radius: 40px;
  background-color: burlywood;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  transition: 0.3s ease-in-out;
  border: 2px solid white;
  cursor: pointer;
  align-items: center;
}
.inputS > .inputS__round {
  width: 30px;
  height: 100%;
  background-color: white;
  border-radius: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;
  border: none;
  cursor: pointer;
}

@media only screen and (min-width: 280px) and (max-width: 780px) {
  .inputS {
    margin-bottom: 20px;
  }
}

.landing {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.landing__content {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing__content > h1 {
  font-size: 72px;
  font-weight: 900;
  text-align: center;
}
.landing__content > p {
  width: 90%;
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: 500;
  padding: 20px;
  text-align: center;
  font-family: Roboto, serif;
}
.landing__image {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing__image > lottie-player {
  width: 500px;
  height: 500px;
  margin-bottom: 30px;
  border-radius: 20px;
}
@media only screen and (min-width: 200px) and (max-width: 780px) {
  .landing {
    flex-direction: column-reverse;
  }
  .landing__content {
    justify-content: flex-start;
  }
  .landing__content > h1 {
    font-size: 44px;
    margin-top: 40px;
  }
  .landing__image {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
  .landing__image > lottie-player {
    width: 250px;
    height: 200px;
    margin-top: 20px;
  }
}

.create {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
}
.create__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 35%;
  margin-top: 20px;
  margin-bottom: 50px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  border-radius: 10px;
  border: 1px solid blue;
  /* box-shadow: 0 5px 10px rgb(131, 131, 131); */
  transition: 0.3s ease-in-out;
}
.create__content > form .create__input {
  display: flex;
  margin-left: 5%;
  margin-right: 5%;
  flex-direction: column;
  /* background-color: #464646e7; */
}
.create__input > input {
  width: 90%;
  margin-right: 5%;
  outline-width: 0px;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: rgba(191, 203, 219, 0.438);
  color: #0b071d;
}
.create__input:nth-child(1) {
  margin-top: 1%;
}
.create__input:last-child {
  margin-bottom: 2%;
}
.create__input > p {
  font-size: 18px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding: 5px;
  margin-top: 10px;
}
.checkbox {
  margin-top: 20px;
}
.create__input > .check {
  padding: 10px 20px;
  margin-left: 0px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-top: 20px;
  margin-bottom: 10px;
}
.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin-top: 20px;
  margin-bottom: 20px;
}
@keyframes spin {
  0% { /* Chrome, Opera 15+, Safari 3.1+ */ /* IE 9 */
    transform: rotate(0deg); /* Firefox 16+, IE 10+, Opera */
  }
  100% { /* Chrome, Opera 15+, Safari 3.1+ */ /* IE 9 */
    transform: rotate(360deg); /* Firefox 16+, IE 10+, Opera */
  }
}

@media only screen and (min-width: 180px) and (max-width: 780px) {
  .create__content {
    width: 90%;
  }
}

