.landing {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.landing__content {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing__content > h1 {
  font-size: 72px;
  font-weight: 900;
  text-align: center;
}
.landing__content > p {
  width: 90%;
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: 500;
  padding: 20px;
  text-align: center;
  font-family: Roboto, serif;
}
.landing__image {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing__image > lottie-player {
  width: 500px;
  height: 500px;
  margin-bottom: 30px;
  border-radius: 20px;
}
@media only screen and (min-width: 200px) and (max-width: 780px) {
  .landing {
    flex-direction: column-reverse;
  }
  .landing__content {
    justify-content: flex-start;
  }
  .landing__content > h1 {
    font-size: 44px;
    margin-top: 40px;
  }
  .landing__image {
    height: max-content;
  }
  .landing__image > lottie-player {
    width: 250px;
    height: 200px;
    margin-top: 20px;
  }
}
